.button-link {
    color: #1c1c1c;
    padding: 0.8em;
    border: 0.2em solid #1c1c1c;
    border-radius: 0.3em;
    text-decoration: none;
    outline: none;
    margin: 1em;
  }
  
.button-link:visited {
    color: #1c1c1c;
  }
  
.button-link:hover {
    transition-delay: 0s, 0s;
    transition-duration: 0.17s, 0.17s;
    transition-property: color, background-color;
    transition-timing-function: ease-in-out, ease-in-out;
    background: #1c1c1c;
    color: #f8f8f8;
  }

